
@import url(../../assets/iconfont/iconfont.css);
.wv-spinner[data-v-067ccc1f]{display:inline-block;overflow:hidden;-webkit-animation:circle 1.2s infinite linear;-o-animation:circle 1.2s infinite linear;animation:circle 1.2s infinite linear
}
@-webkit-keyframes circle{
0%{transform:rotate(0deg)
}
100%{transform:rotate(360deg)
}
}



